'use strict';

//  
const createHomeView = (name) => {
  return (resolve) => {
    return require(['@pc/views/article/' + name + '.vue'], resolve);
  };
};

const indexView = createHomeView('Index');

export const categoryArticleRouters = [
  {
    path: '/all',
    name : '',
    component: indexView
  },
  {
    path: '/technology',
    name : '',
    component: indexView
  },
  {
    path: '/skating',
    name : '',
    component: indexView
  },
  {
    path: '/guitar',
    name : '',
    component: indexView
  },
  {
    path: '/travel',
    name : '',
    component: indexView
  },
  {
    path: '/life',
    name : '',
    component: indexView
  },
  {
    path: '/books',
    name : '',
    component: indexView
  }
];

export default [
  ...categoryArticleRouters,
  {
    path: '/articleDetails/:id',
    component: createHomeView('ArticleDetails'),
    meta: { title : '', scrollTop : 0}
  }
];



