'use strict';

const SPECIAL_CHARS_REGEXP = /([\:\-\_]+(.))/g;
const MOZ_HACK_REGEXP = /^moz([A-Z])/;

/**
 *  - 
 * @param name
 * @returns {string}
 */
export const camelCase = (name) => {
  return name.replace(SPECIAL_CHARS_REGEXP, (_, separator, letter, offset) => {
    return offset ? letter.toUpperCase() : letter;
  }).replace(MOZ_HACK_REGEXP, 'Moz$1');
};


/**
 * arr,1
 * @returns {*}
 **/
export const randomElemnt = (array) => {
  let len = 0;
  if (array.length === len) {
    throw new Error('the array is empty');
  }
  return array[Math.floor(Math.random() * array.length)];
};


/**
 * jpg
 * @return {Boolean}
 **/
export const isJpg = (str) => {
  return /\.jpe?g$/.test(str);
};

/**
 * png
 * @return {Boolean}
 **/
export const isPng = (str) => {
  return /\.png$/.test(str);
};

/**
 * 
 * @param file 
 * @param $message element-ui
 * @return {Boolean}
 **/
export const isTypeImage = (file, $message) => {
  const isImage = isJpg(file.name) || isPng(file.name);
  const isLt2M = file.size / 1024 / 1024 < 2;

  if (!isImage) {
    $message.error(' JPG  PNG !');
  }
  if (!isLt2M) {
    $message.error(' 2MB!');
  }
  return isImage && isLt2M;
};


/**
 * 
 * @param {Array} arr
 * @param {Object} obj
 * @param {String} attr
 * @return {Number}
 **/
export const indexOfByAttr = (arr, obj, attr) => {
  let i = arr.length;
  while (i--) {
    if (arr[i] && arr[i][attr] === obj[attr]) {
      return i;
    }
  }
  return -1;
};

