/**
 * 
 * @param {Array} arr
 * @return {Array}
 **/
export const unique = (arr) => {
  return Array.from(new Set(arr));
};


/**
 * 
 * @param {Array}  array       
 * @param {string} indexOfAttr 
 * @param {Array}  currArray   
 * @param {Array}  newArray    
 */
export const toggleArray = (targetArray, indexOfAttr, currArray, newArray) => {
  if (Array.isArray(targetArray) === false) {
    throw new Error(' targetArray ');
  }
  if (targetArray.length === 0) {
    return;
  }

  targetArray.forEach((item) => {
    let index = indexOfByAttr(currArray, item, indexOfAttr);

    if (index !== -1) {
      currArray.splice(index, 1);
    }

    if (newArray) {
      newArray.push(item);
    }
  });
};


/**
 * 
 * @param date       //
 * @param fmt        // 'yyyy-M-d'  'yyyy-MM-dd'  'yyyy-MM-dd hh:mm:ss'
 * @returns {String}
 */
export const dateFormat = (date, fmt) => {
  let o = {
    'M+': date.getMonth() + 1, //
    'd+': date.getDate(), //
    'h+': date.getHours(), //
    'm+': date.getMinutes(), //
    's+': date.getSeconds(), //
    'q+': Math.floor((date.getMonth() + 3) / 3), //
    'S': date.getMilliseconds() //
  };

  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
  }

  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
    }
  }

  return fmt;
};
/**
 * days
 * @param {Data} date 
 * @param {days} days days
 * @returns days
 */
export const dateOperator = (date, days) => {
  date = date.replace(/-/g, '/'); //
  let times = new Date(date);
  times = times.valueOf();
  times = times - days * 24 * 60 * 60 * 1000;
  times = new Date(times);

  let y = times.getFullYear();
  let m = times.getMonth() + 1;
  let d = times.getDate();
  if (m <= 9) {
    m = '0' + m;
  }
  if (d <= 9) {
    d = '0' + d;
  }
  let cdate = y + '-' + m + '-' + d;
  return cdate;
};


/**
 *   
 * @param text     
 * @param maxLen   
 * @returns {String}
 */
export const textEllipsis = (text, maxLen) => {
  if (typeof text !== 'string') {
    throw new Error(' text ');
  }

  let textLen = text.length || 0;
  maxLen = maxLen || 50;

  if (textLen > maxLen) {
    text = text.substr(0, maxLen) + '...';
  }
  return text;
};

/**
 *   
 * @param ary   
 * @param fn    
 * @param count 
 * @param waitTime 
 **/
export const timeChunk = (ary, fn, count, waitTime) => {
  //
  if (Array.isArray(ary) !== 'array') {
    throw Error(' ary ');
  }

  let timer;
  //
  let start = () => {
    for (let i = 0; i < Math.min(count || 1, ary.length); i++) {
      let obj = ary.shift();
      if (fn) {
        fn(obj);
      }
    }
  };

  return function () {
    timer = setInterval(() => {
      if (ary.length === 0) { // 
        return clearInterval(timer);
      }
      start();
    }, waitTime || 200);
  };
};



