'use strict';

import Vue from 'vue';
import VueRouter from 'vue-router';     //
import {indexOfByAttr} from '@/common/utils';

Vue.use(VueRouter);

import articleRouter from '@pc/router/article';
import musicRouter from '@pc/router/music';

let routes = [
  ...articleRouter,
  ...musicRouter
];

const router = new VueRouter({
  routes: routes
});

/**
 * 
 * @param to Route:  
 * @param from  Route: 
 * @param next  Function:  resolve  next 
 * @returns {boolean}
 */
const checkRouterPath = (to, from, next) => {
  let toPath = to.path;
  let isChildrenPath = false;

  //
  for (let i = 0, len = routes.length; i < len; i++) {
    let route = routes[i];
    let children = route.children;

    if (children && indexOfByAttr(children, to, 'path') !== -1) {
      isChildrenPath = true;
      break;
    }
  }

  //  
  if (isChildrenPath === false && indexOfByAttr(routes, to, 'path') === -1) {
    let index = toPath.indexOf('/', 1);
    let like = {
      path: index !== - 1 ? index : toPath
    };

    if (indexOfByAttr(routes, like, 'path') === -1) {
      next({path: '/all'});
    } else {
      next({path: like.path});
    }
    return false;
  }

  return true;
};

//
router.beforeEach((to, from, next) => {
  let title = to.meta.title;
  let scrollTop = to.meta.scrollTop;
  if (!title) {
    title = '';
  }

  //
  if (checkRouterPath(to, from, next) === false) {
    return;
  }

  if (scrollTop !== undefined) {
    document.body.scrollTop = scrollTop;
  }

  document.title = title;

  next();
});


export default router;



